#location__main-container {
  display: flex;
  width: 80vw;
  height: 100vh;
  margin: 3rem auto;

  & .shop__locations {
    width: 100%;
    text-align: center;

    & .location-table {
      width: 100%;
      text-align: center;
      border: none;
      border-collapse: collapse;
      margin-bottom: 4rem;
      box-shadow: 5px 6px 18px #888888;

      & tr td {
        border: 1px solid lightgray;
        padding: 1rem;
      }
      & .table__head th {
        padding: 1rem 0;
        background-color: $nav-container-bg;
        color: #fff;
        border: none;
      }

      & tbody {
        margin-top: 1rem;
        // grab the inner div inside a td
        & tr td > div {
          position: relative;
          & .tooltip__icon__container {
            width: 1.5rem;
            position: absolute;
            top: 0;
            right: 0;
            & #bookmarker {
              font-size: 1.5rem;
              color: #a37eba;
            }
            & i:hover {
              cursor: pointer;
            }
          }

          & .shop__list__container {
            display: flex;
            flex-direction: row;
            align-items: center;
            & .sub__division {
              width: 100%;
              height: 100%;
              display: flex;
              justify-content: space-evenly;

              & a {
                display: block;
                padding: 0.5rem;
                border-radius: 5px;
                background-color: $nav-container-bg;
                color: #fff;
                text-decoration: none;

                &:hover {
                  transform: scale(1.1);
                  transition: 0.2s ease-in-out;
                }
              }
            }
          }
        }
      }
    }
  }
}

img {
  max-width: 200px;
  border-radius: 5px;
}

ul {
  list-style-type: none;
}
