.nav__container {
  // the body is relatively positioned, so, we can absolute position here
  position: fixed;
  top: 0;
  width: 100%;
  display: flex;
  z-index: 1; // always show at top level

  & .nav__list-container {
    display: flex;
    justify-content: flex-end;
    list-style-type: none;
    width: 100%;
    height: 4rem;
    background-color: $nav-container-bg;
    position: relative;

    & .logo {
      position: absolute;
      top: 0.55rem;
      left: 1rem;
      font-size: 2.5rem;
      color: #fff;
    }

    & .site__logo:hover {
      cursor: pointer;
      transform: scale(1.1);
      transition: 0.1s all ease-in-out;
    }

    // nested style - this li is a child of the container so we can save code and use & to prefix this selector with the parent selector name
    & .list__items {
      display: flex;
      height: 100%;
      width: 6rem;
      margin: 0 0.5rem;

      & a {
        display: flex;
        align-items: center;
        justify-content: center;
        color: #fff;
        text-decoration: none;
        height: 100%;
        width: 100%;
      }

      &:hover {
        cursor: pointer;
        box-shadow: inset 0 -4px 0 0 #eee8e8;
      }
    }
  }
}
