@import "utilities"; // styling for the reset
@import "config"; //default styles and fonts
@import "nav"; // nav set
@import "hero"; // hero set
@import "login"; // login styles
@import "location"; //location styling
@import "dialog"; //dialog styles

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: $font-stack;
}

// basic body styles
html,
body {
  background-color: rgb(238, 232, 232);
  position: relative;
  height: 100%;
}

// make the container a grid container to properly display each item nicely
.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  width: 85%;
  margin: 0 auto;
  margin-top: 3rem;
  margin-bottom: 3rem;

  & .section__content {
    display: flex;
    justify-content: space-evenly;
    align-items: center;

    & p {
      display: block;
      font-size: 1.2rem;
      padding: 1rem;
    }

    & img {
      object-fit: cover;
      border-radius: 5px;
      width: 200px;
      height: 200px;
    }
  }

  & .card {
    padding: 0.5rem;
    width: 500px;
    min-height: 200px;
    border: 1px solid lightgray;
    box-shadow: 5px 6px 18px #888888;
    border-radius: 5px;
    margin-bottom: 1.5rem;
  }
}
.section__content:hover {
  transform: translate(0, -5px);
  transition: 0.1s ease;
}

.about__content {
  display: flex;
  width: 50%;
  margin: 0 auto;
  margin-top: 2rem;
  line-height: 3rem;
  text-align: center;
}
.footer__content {
  display: flex;
  width: 100vw;
  height: 2.5rem;
  background-color: rgb(34, 29, 29);
  color: #fff;
  font-size: 0.8rem;
  justify-content: center;
  align-items: center;
  position: fixed;
  bottom: 0;
}
